@import url(https://fonts.googleapis.com/css2?family=Cantarell&family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins);
/* Navbar */

.navbar-wrapper {
  display: flex;
  flex-direction: row;
  height: 9vh;
  box-shadow: rgba(133, 129, 129, 0.35) 0px 2px 5px;
  justify-content: space-between;
  overflow: hidden;
  padding: 5px;
  position: fixed; /* Set the navbar to fixed position */
  top: 0;
  width: 100%;
  background-color: white;
}

.navbar-wrapper ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.navbar-wrapper > img {
  margin: 7px;
}

.toggle-button {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 40px;
  height: 25px;
  background-color: white;
  border: none;
  cursor: pointer;
}

.toggle-button .bar {
  width: 100%;
  height: 3px;
  border-radius: 10px;
  background-color: #1e1f1f;
}

.navbar-wrapper ul li {
  margin: 0 10px;
}
.navbar-wrapper ul li a {
  text-decoration: none;
  color: inherit;
  position: relative;
  cursor: pointer;
}

@media (max-width: 600px) {
  .navbar-wrapper > img {
    margin: 10px;
    height: 40px;
  }

  .toggle-button {
    display: flex;
  }

  .navbar-wrapper ul {
    display: none;
  }

  .navbar-wrapper ul {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    background-color: white;
    flex-direction: column;
    position: fixed;
    top: 70px;
    left: 10%;
    width: 80%;
    text-align: center;
    border-radius: 5px;
  }

  .navbar-wrapper ul li {
    margin: 15px;
  }

  .button-toggle {
    display: flex !important;
  }
}

.navbar-wrapper ul li a::after {
  content: "";
  background-color: #2c405e;
  position: absolute;
  left: 0;
  bottom: -18px;
  height: 2px;
  width: 100%;
  opacity: 0;
  transition: transform 0.2s ease;
}

.navbar-wrapper ul li a:hover::after {
  transform: translateY(-15px);
  opacity: 1;
}

